.reviews__wrapper {
  padding-left: 31px;
  line-height: 30px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.reviews__title {
  font-weight: bold;
  font-size: 25px;

  @media (min-width: @tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: @desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}

.reviews__item {
  @media (min-width: @tablet-width) {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

.reviews__text {
  font-size: 15px;
  color: @brown-text;
}

.reviews__author-name {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  font-style: normal;
}

.reviews__author-email {
  font-size: 17px;
}

.reviews__write-review-button {
  padding: 16px 65px;

  @media (min-width: @tablet-width) {
    grid-column-start: 2;
    grid-row-start: 1;
  }
}
